import React from "react";

const Footer = props => {
  return (
    <footer className="footer">
      <div className="content has-text-centered">
        <p>
          <i>ToDo List by Krzysztof Kamieniecki</i>
        </p>
      </div>
    </footer>
  );
};

export default Footer;



